<template>
  <div>
    <!-- <div class="title">default</div>
    <address-picker v-on:change="getValue">:</address-picker>

    <div class="title">hasCounty : false</div>
    <address-picker v-on:change="getValue" :hasCounty="false">:</address-picker> -->


    <div class="title" :value="value">value : ["","",""]</div>
    <address-picker v-on:change="getValue" :value="value">:</address-picker>

  </div>
</template>


<script type="text/javascript">

  import address from '../../packages/addressPicker';
  export default {
      data() {
          return {
              value:['','',''],
          };
      },
      //
      created(){

      },
      methods:{
          //get message from child component
          getValue(msg){
              console.log(msg);
          }
      },
      components:{
          addressPicker : address
      }
  };
</script>





